* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  position: relative;
  height: 100vh;
  background: url(../img/wallpaper_small.jpg) center/cover no-repeat;
  @each $member in top, bottom, left, right {
    .#{$member} {
      #{$member}: 0;
    }
  }

  #main-view {
    position: absolute;
    transform: translate(-50%, -50%);
    box-shadow: rgba(0, 0, 0, 0.31) 0px 0px 1px, rgba(0, 0, 0, 0.18) 0px 0px 5px,
      rgba(0, 0, 0, 0.3) 0px 8px 50px;
  }

